import React from 'react';
import { subVideoData } from '../data/data';
import { heroVideoData } from '../data/data';
import './subvideo.scss';

class SubVideo extends React.Component {

    //   let newsubVideoData = this.props.subVideoData.filter(item => item.id !== this.props.mainVideoData.id);

    render () {
        console.log(subVideoData);
        console.log(heroVideoData);
        let newSubVideoData = this.props.subVideoData.filter(item => item.id !== this.props.heroVideoData.id);

        return(
            <aside className="subvideo">
                <p className="sub-video__title">NEXT VIDEO</p>
                {
                    newSubVideoData.map ( (item, i) => {
                    return (
                    <div className="sub-videos__photo" key={item.id}>
                        <video className="sub-video__video-container__video" poster={item.image}></video>
                        <div className="sub-videos__video-container__photo-information">
                            <p className="video-container__photo-information__title">{item.title}</p>
                            <p className="video-container__photo-information__channel">{item.channel}</p>
                        </div>
                    </div>
                    )
                })
            }
            </aside>
        )
    }
}


export default SubVideo;

