
import React from 'react';

import Header from '../header/Header';
import HeroVideo from '../herovideo/herovideo';
import About from '../about/about';
import Input from '../input/input';
import Form from '../form/form';
import SubVideo from '../subvideo/subvideo';



import {heroVideoData, subVideoData, CommentData} from '../data/data';
import './main.scss';

class Main extends React.Component {

    state = {
        heroVideoData: heroVideoData,
        subVideoData: subVideoData,
        CommentData: CommentData
    }

    render() {
        return (
            <div className="main"> 
                <Header />
                <HeroVideo heroVideoData={this.state.heroVideoData}/>
                <div className="main__layout">
                    <div className="main__layout__container">
                        <About heroVideoData={this.state.heroVideoData}/>
                        <Input CommentData={this.state.CommentData}/>
                        <Form CommentData={this.state.CommentData} />
                    </div>
                    <SubVideo sideVideoData={subVideoData} heroVideoData={heroVideoData}/>
                </div>
            </div> 
        );  
    }
}



export default Main;